import helper from '../../common/helper.js';
export default {
	data() {
		return {
			shows: 0,
			rwArray: {},
			rwbzArray: {},
		}
	},
	onLoad(e) {         //e接收任务的id
		this.shrwlist(e)
	},
	methods: {
		// 领取任务列表
		shrwlist(e) {
			helper.islogin(true);
			uni.showLoading({
				title: "加载中....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'task/getmytask',
				method: "GET",
				data: helper.postdata({
					id: e.shid,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.rwArray = res.data.data.baseinfo;
						this.rwbzArray = res.data.data.baseinfo.Instruction.split(";");
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请重试！'
					});
				}
			})
		},

	}
}
